<template>
  	<!-- set a computed part in export default -->
  	<!-- to access the store mutation -->
    
  	<!-- set a element as a transition containner between the setup function and -->
    <!-- the  condition to cahnge the store -->


  <h1>STORETEST</h1>
  <h2 ref="storeTransmeter">I m the store transmiter</h2>
  <h2 v-if=" state.stateStoreTransmeter=='1'">{{this.$store.commit('setUserIsLogged')}}</h2>
  <h2 v-else>{{this.$store.commit('setUserIsNotLogged')}}</h2>

  <button @click="logIn()">logIn</button>
  <button @click="logOut()">logOut</button>
 
</template>
<script>
  import {computed,ref,reactive} from 'vue'


export default {
  computed: {
    complogIn() {
      console.log('I do  computed logIn')
      return this.$store.commit('setUserIsLogged')

    },
    complogOut() {
      console.log('I do computed logOut')
      return this.$store.commit('setUserIsNotLogged')
    },
  },
  setup() {
    const storeTransmeter = ref('');
    const state = reactive({
      stateStoreTransmeter: 'default',
    })
    
    const logIn = () => {
      console.log("you are logged in")   
      storeTransmeter.value.style.color='green'
      state.stateStoreTransmeter = 1
		}
    const logOut = () => {
      console.log("you are logged out")   
      storeTransmeter.value.style.color='red'
      state.stateStoreTransmeter = 0
		}
   return {logIn,logOut,storeTransmeter,state}
  }
  }
</script>